/*::::::::
Angle
::::::::*/
function Angle(InputValue, InputUnit, OutputUnit = "R") {
	let SIValue, OutputValue;
	let Invalid = false;
	if (typeof InputValue === "number") {
		switch (InputUnit) {
			case "rad": case "R": case "RAD": case "Radian": case "radian": // SIUnit
				SIValue = InputValue;
				break;
			case "°": case "D": case "Degree": case "degree":
				SIValue = InputValue * (Math.PI/180);
				break;
			case "g": case "G": case "gon": case "ᵍ": case "grad": case "grade": case "Gradian": case "gradian":
				SIValue = InputValue * (Math.PI/200);
				break;
			case "tr": case "T": case "pla": case "Turn": case "turn":
				SIValue = InputValue * (2*Math.PI);
				break;
			default:
				OnError_InputUnit("Angle", InputUnit);
				Invalid = true;
				SIValue = 1;
				break;
		};
		switch (OutputUnit) {
			case "rad": case "R": case "RAD": case "Radian": case "radian": // SIUnit
				OutputValue = SIValue;
				break;
			case "°": case "D": case "Degree": case "degree":
				OutputValue = SIValue / (Math.PI/180);
				break;
			case "g": case "G": case "gon": case "ᵍ": case "grad": case "grade": case "Gradian": case "gradian":
				OutputValue = SIValue / (Math.PI/200);
				break;
			case "tr": case "T": case "pla": case "Turn": case "turn":
				OutputValue = SIValue / (2*Math.PI);
				break;
			default:
				OnError_OutputUnit("Angle", OutputUnit);
				Invalid = true;
				OutputValue = undefined;
				break;
		};
	} else {
		OnError_InputValue("Angle", InputValue, "number");
		OutputValue = undefined;
	};
	if (Invalid == true) {
		OutputValue = undefined;
	};
	return OutputValue;
};

/*::::::::
Weight
::::::::*/
function Weight(InputValue, InputUnit, OutputUnit = "kg") {
	let SIValue, OutputValue;
	let Invalid = false;
	if (typeof InputValue === "number") {
		switch (InputUnit) {
			case "kg": case "㎏": case "Kilogram": // SIUnit
				SIValue = InputValue;
				break;
			case "g": case "Gram":
				SIValue = InputValue / 1000;
				break;
			case "t": case "Tonne":
			case "Mg": case "Megagram":
				SIValue = InputValue * 1000;
				break;
			case "斤": case "catty": case "Catty":
				SIValue = InputValue * 0.60478982;
				break;
			case "lb": case "Pound":
				SIValue = InputValue / 2.20462262;
				break;
			case "兩": case "Tael":
				SIValue = InputValue / 16 * 0.60478982;
				break;
			case "錢": case "Mace":
				SIValue = InputValue / 160 * 0.60478982;
				break;
			case "oz": case "OZ": case "Ounce":
				SIValue = InputValue * 28.349523125 / 1000;
				break;
			case "eV": case "ElectronVolt":
				SIValue = InputValue * (1.783 * Math.pow(10, -36));
				break;
			default:
				OnError_InputUnit("Weight", InputUnit);
				Invalid = true;
				SIValue = 1;
				break;
		};
		switch (OutputUnit) {
			case "kg": case "㎏": case "Kilogram": // SIUnit
				OutputValue = SIValue;
				break;
			case "g": case "Gram":
				OutputValue = SIValue * 1000;
				break;
			case "t": case "Tonne":
			case "Mg": case "Megagram":
				OutputValue = SIValue / 1000;
				break;
			case "斤": case "catty": case "Catty":
				OutputValue = SIValue / 0.60478982;
				break;
			case "lb": case "Pound":
				OutputValue = SIValue * 2.20462262;
				break;
			case "兩": case "Tael":
				OutputValue = SIValue / 0.60478982 * 16;
				break;
			case "錢": case "Mace":
				OutputValue = SIValue / 0.60478982 * 160;
				break;
			case "oz": case "OZ": case "Ounce":
				OutputValue = SIValue / 28.349523125 * 1000;
				break;
			case "eV": case "ElectronVolt":
				OutputValue = SIValue / (1.783 * Math.pow(10, -36));
				break;
			default:
				OnError_OutputUnit("Weight", OutputUnit);
				Invalid = true;
				OutputValue = undefined;
				break;
		};
	} else {
		OnError_InputValue("Weight", InputValue, "number");
		OutputValue = undefined;
	};
	if (Invalid == true) {
		OutputValue = undefined;
	};
	return OutputValue;
};

/*::::::::
Radix
::::::::*/
function Radix(InputValue, InputRadix, OutputRadix = 10) {
	let SIValue, OutputValue;
	if ((Number.isInteger(InputValue) === true && InputValue >= 0) || (typeof InputValue === "string" && InputValue.charAt(0) != "-")) {
		if (Number.isInteger(InputRadix) === true && InputRadix >= 2 && InputRadix <= 36 && Number.isInteger(OutputRadix) === true && OutputRadix >= 2 && OutputRadix <= 36) {
			SIValue = parseInt(InputValue, InputRadix);
			if (Number.isNaN(SIValue) === false) {
				OutputValue = SIValue.toString(OutputRadix);
				if (OutputRadix >= 2 && OutputRadix <= 10) {
					OutputValue = Number(OutputValue);
				};
			} else {
				OutputValue = undefined;
			};
		} else {
			OutputValue = undefined;
			if (Number.isInteger(InputRadix) !== true || InputRadix < 2 || InputRadix > 36) {
				console.error("%cJavaScript Macro - Converter", "font-weight: bold;", "\n", "Unknown input unit \"" + JSON.stringify(InputRadix) + "\" in the \"Radix\" converter! Input unit must be type of number, integer, and between 2 and 36!");
			};
			if (Number.isInteger(OutputRadix) !== true || OutputRadix < 2 || OutputRadix > 36) {
				console.error("%cJavaScript Macro - Converter", "font-weight: bold;", "\n", "Unknown output unit \"" + JSON.stringify(OutputRadix) + "\" in the \"Radix\" converter! Output unit must be type of number, integer, and between 2 and 36!");
			};
		};
	} else {
		OnError_InputValue("Radix", InputValue, "number, integer, and positive number; or string");
		OutputValue = undefined;
	};
	if (Number.isNaN(OutputValue) === true) {
		OutputValue = undefined;
	};
	return OutputValue;
};

/*::::::::
Package Module
::::::::*/
export {
	Temperature,
	Length,
	Angle,
	Weight,
	Radix
};
